<!--eslint-disable max-len-->
<!--eslint-disable vuejs-accessibility/label-has-for-->
<template>
  <li class="catalog__item">
            <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}">
              <img :src="product.image" :alt="product.title">
            </router-link>

            <h3 class="catalog__title">
              <a href="#">
                {{ product.title }}
              </a>
            </h3>

            <span class="catalog__price">
              {{ product.price | numberFormat }}
            </span>

            <ul class="colors colors--black" >
              <li class="colors__item" v-for="color in colorsData" :key="color.id">
                <label class="colors__label" >
                  <input class="colors__radio sr-only" type="radio" name="color" :value="color.id">
                  <span class="colors__value"  :style="{'background-color': color.code}" >
                  </span>
                </label>
              </li>
            </ul>
          </li>
</template>

<script>

import goToPage from '@/helpers/goToPage';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';

export default {
  data() {
    return {
      // colors,
      colorsData: null,
    };
  },
  computed: {
    colors() {
      return this.colorsData ? this.colorsData.code : [];
    },
  },
  methods: {
    goToPage,
    loadColors() {
      axios.get(`https://vue-study.skillbox.cc/api/products/${this.product.id}`)
        .then((response) => this.colorsData = response.data.colors);
    },
  },
  created() {
    this.loadColors();
  },
  filters: {
    numberFormat,
  },
  props: ['product'],
};
</script>
